<template>
  <div class="docs-view">
    <a
      id="nav-button"
      :class="{ open: navOpen }"
    >
      <span @click="navOpen = !navOpen"> NAV <img :src="$getAsset('navbar.png')"> </span>
    </a>
    <div
      :class="{ open: navOpen }"
      class="tocify-wrapper fade-in"
    >
      <router-link to="/">
        <img
          :src="$getAsset('ijs.svg')"
          class="logo"
          alt="interact.js"
          title="homepage"
        >
      </router-link>

      <div class="has-text-centered">
        <VersionBadge />
      </div>

      <div class="search">
        <input
          id="input-search"
          type="text"
          class="search"
          placeholder="Search"
        >
      </div>
      <ul class="search-results" />

      <div
        id="toc"
        ref="toc"
      />

      <br class="is-hidden-tablet">

      <nav
        class="navbar is-hidden-tablet"
        role="navigation"
        aria-label="navigation"
      >
        <HeaderLinks class="navbar-end" />
      </nav>
    </div>
    <div class="page-wrapper">
      <nav
        class="navbar is-dark fade-in"
        role="navigation"
      >
        <HeaderLinks
          mobile
          class="level-right is-dark navbar-end is-flex is-hidden-mobile"
          aria-label="navigation"
        />
      </nav>

      <div class="dark-box" />

      <keep-alive
        :key="$route.path"
        :max="3"
      >
        <router-view class="docs-content fade-in dur-point-25" />
      </keep-alive>

      <div class="ad-padding" />
    </div>
  </div>
</template>

<style lang="sass" src="@/sass/slate.sass"/>

<script>
import Vue from 'vue'

export default Vue.extend({
  data () {
    return { navOpen: false }
  },
  computed: {
    path () { return this.$route.path }
  },
  metaInfo: { title: 'Docs' },
  watch: {
    path () {
      this.initToc()
    }
  },
  mounted () {
    this.initToc()
  },
  destroyed () {
    this.toc.destroy()
    this.toc = null
  },
  methods: {
    async initToc () {
      if (this.toc) { this.toc.destroy() }
      if (this._inactive) { return }

      const { default: tocbot } = await import('tocbot')

      this.toc = tocbot.init({
        tocSelector: '#toc',
        headingSelector: 'h1, h2, h3',
        contentSelector: '.page-wrapper',
        ignoreSelector: '.js-toc-ignore',
        scrollSmooth: true,
        scrollSmoothDuration: 420,
        collapseDepth: 6
      })
    }
  },
  beforeRouteLeave (to, from, next) {
    this.toc.destroy()
    next()
  }
})
</script>
